.container {
  max-width:600px;
  margin: 0 auto;
  text-align: center;
  background: #C9CCD3;
  background: linear-gradient(to bottom, rgba(19, 19, 19, 0.541) 0%, rgba(0, 0, 0, 0.315) 100%), radial-gradient(at top center, rgba(255,255,255,0.40) 0%, rgba(0,0,0,0.40) 120%) #989898;
  background-blend-mode: multiply,multiply;
}


